

































































	import graceLoading from '../../graceUI/components/graceLoading.vue';
import uniLoadMore from	'@dcloudio/uni-ui/lib/uni-load-more/uni-load-more.vue';
export default {
	 components: {graceLoading,uniLoadMore},
	data() {
		return {
			// articleAll:[],
			articles: [],
			tabCurrentIndex: 0,
			swiperCurrentIndex: 0,
			titleShowId: 'tabTag-0',
			page:1,
			tabs: [
				//标签名称 , 分类 id , 加载更多, 加载的页码
				{ name: '推荐', id: 'pwd1', loadingType: 0, page: 1 },
				{ name: '榜单', id: 'pwd2', loadingType: 0, page: 1 },
				{ name: '专题', id: 'pwd3', loadingType: 0, page: 1 },
				{ name: '连载', id: 'pwd3', loadingType: 0, page: 1 }
			],
			showKeyboard: false
		};
	},
	onLoad: function() {
		this.getArticles();
	},
	onShow: function() {},
	onPullDownRefresh: function() {
		this.getArticles();
	},
// 	onReachBottom:function(){
// 		this.getArticles();
// 	},
	methods: {
		confirmPwd() {
			this.$refs.keyboard.show();
		},
		onInput(e) {
			//e.cancel = true;
		},
		onConfirm(e) {
			let password = e.value;
		},
		tabChange(e) {
			var index = e.target.id.replace('tabTag-', '');
			this.swiperCurrentIndex = index;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		},
		swiperChange: function(e) {
			var index = e.detail.current;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		},
		gotoWrite: function() {
			if (uni.getStorageSync('login_key').login === true) {
				uni.navigateTo({
					url: '../write/write'
				});
			} else {
				uni.showModal({
					title: '提示',
					content: '您未登录',
					cancelText: '再等等',
					confirmText: '前往登录',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: '../signin/signin'
							});
						}
					}
				});
			}
		},
		getArticles: function() {
			var _this = this;
			uni.request({
				url: this.apiServer + '/article/list',
				method: 'GET',
// 				data:{
// 					page:_this.page,
// 					size:5
// 				},
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				success: res => {
					_this.articles = res.data.data;
					for (var i = 0; i < _this.articles.length; i++) {
						_this.articles[i].createTime = this.handleTime(_this.articles[i].createTime);
						_this.articles[i].content = this.handleContent(_this.articles[i].content);
					}
					// _this.articleAll.push(_this.articles);
				},
				complete: function() {
					uni.stopPullDownRefresh();
				}
			});
		},
		gotoDetail: function(aId) {
			uni.navigateTo({
				url: '../article_detail/article_detail?aId=' + aId
			});
		},
		handleTime: function(date) {
			var d = new Date(date);
			var year = d.getFullYear();
			var month = d.getMonth() + 1;
			var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate();
			var hour = d.getHours() < 10 ? '0' + d.getHours() : '' + d.getHours();
			var minutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : '' + d.getMinutes();
			var seconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : '' + d.getSeconds();
			return year + '-' + month + '-' + day + ' ' + hour + ':' + minutes + ':' + seconds;
		},
		handleContent: function(description) {
			description = description.replace(/(\n)/g, '');
			description = description.replace(/(\t)/g, '');
			description = description.replace(/(\r)/g, '');
			description = description.replace(/<\/?[^>]*>/g, '');
			description = description.replace(/\s*/g, '');
			return description;
		},
	}
};
